import styled from 'styled-components';

export const InfoTitle = styled.h4`
  color: ${p => p.theme.primaryColor};
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
  margin: 15px 0;

  ::after {
    content: '';
    border-top: 1px solid;
    margin: 0 0 0 10px;
    flex: 1 0 20px; /* Increase the left and right margins to add space */
   
  }
`;

export const CaseStudyWrapper = styled.section`
  margin-top: 100px;
  margin-bottom: 100px;

  li {
    margin: 5px 0;
  }

  // 
  .case__title {
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;  /* Space out title and link */
  align-items: center;            /* Vertically center items */
  width: 70%;                    /* Ensure the title container takes the full width of its parent */
}

.case__title h1 {
  margin: 0;
  // line-height: 10px;
}

.case__links {
  margin: 0;
  display: flex;
  // align-items: center;            /* Vertically center the GitHub link */
}

.case__links a {
  text-decoration: none;
  margin-left: 20px;              /* Adds space between title and the GitHub link */
}
}
  }

  .case__info {
    margin-bottom: 70px;
    display: grid;
    grid-template-columns: 5fr 2fr;
    grid-gap: 1px;
    text-align: justify;

    @media ${p => p.theme.media.fablet} {
      grid-template-columns: 0.6fr 1fr;
    }
    @media ${p => p.theme.media.tablet} {
      grid-template-columns: 1fr;
    }
  }
  ul {
    list-style-type: disc; /* Ensures bullet points */
    margin-left: 20px;     /* Adds indentation to the list */
    padding-left: 20px;    /* Adds padding for better spacing */
  }

  li {
    margin-bottom: 10px;   /* Adds spacing between list items */
    line-height: 1.5;      /* Improves readability */
}
  .case__iframe-container {
    width: 100%;
    margin: 10px 0;
  }
  h1 {
    font-size: 1.8rem;
    margin-bottom: 50px;
  }
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 20px 0;
    color: ${p => p.theme.primaryColor};
  }
`;
